import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { Camera, CameraOptions } from "@ionic-native/camera";
import { LightNation } from "../common/lightNation";

/**
 * npm:npm install --save @ionic-native/camera
 * ionic:ionic cordova plugin add cordova-plugin-camera
 * :Android | Browser | iOS | Windows
 * 
 * @param {Camera} camera
 * @memberof LightCamera
 */
@Injectable()
export class LightCamera extends LightNation {
  /**
   * 
   * @type {CameraOptions}
   * @memberof LightCamera
   */
  private _options: CameraOptions;

  constructor(public camera: Camera, private platform: Platform) {
    super();
    this._options = {
      quality: 100, //
      destinationType: this.camera.DestinationType.FILE_URI, ////DATA_URLbase64,FILE_URIURINATIVE_URIios
      sourceType: this.camera.PictureSourceType.CAMERA, //
      allowEdit: true, //
      encodingType: this.camera.EncodingType.JPEG, //
      mediaType: this.camera.MediaType.PICTURE, //sourceType,this.camera.MediaType.ALLMEDIA
      saveToPhotoAlbum: true // };
    };
  }

  /**
   * 
   *
   * @param {*} [resultDefault=null]
   * @returns {Promise<any>}
   * @memberof LightCamera
   */
  getPicture(resultDefault: any = null): Promise<any> {
    var that = this;
    if (!!!resultDefault) {
      resultDefault = "https://www.baidu.com/img/bd_logo1.png";
    }
    var result: Promise<any> = this._getDefaultResult(resultDefault);
    if (this.platform.is("cordova")) {
      if (!!that.camera) {
        result = new Promise(function(resolve, reject) {
          that.camera
            .getPicture(that._options)
            .then(imageURI => {
              resolve(imageURI);
            })
            .catch(errText => {
              reject(errText);
            });
        });
      } else {
        alert("");
      }
    }
    return result;
  }

  /**
   * 
   *
   * @param {*} [resultDefault=null]
   * @returns {Promise<any>}
   * @memberof LightCamera
   */
  // setOptions(options: CameraOptions): Promise<any> {
  //     var that = this;
  //     if (!!!options) {
  //         options = this._options;
  //     }
  // var result: Promise<any> = this._getDefaultResult(resultDefault);
  // if (this.platform.is("cordova")) {
  //     if (!!that.camera) {
  //         result = new Promise(function (resolve, reject) {
  //             that.camera.getPicture(that._options).then(imageURI => {
  //                 resolve(imageURI);
  //             }).catch(errText => {
  //                 reject(errText);
  //             });
  //         });
  //     }
  //     else {
  //         alert("");
  //     }
  // }
  // return result;
  // }
}



