import { Injectable } from "@angular/core";
import { Platform, ToastController, NavController } from "ionic-angular";
import { JPush } from "@jiguang-ionic/jpush";
import { LightNation } from "../common/lightNation";
import {
  LightHttp,
  LightProviders
} from "../../light-providers/light-providers.module";

/**
 * npm: npm install --save @jiguang-ionic/jpush
 * ionic: ionic cordova plugin add jpush-phonegap-plugin --variable APP_KEY=your_jpush_appkey
 * :Android | iOS
 * 
 * @param {JPush} jPush
 * @memberof LightJPush
 */
@Injectable()
export class LightJPush extends LightNation {
  private clickToastCtrl: boolean = false;

  private regId: string = "";

  constructor(
    public platform: Platform,
    public jPush: JPush,
    public toastCtrl: ToastController
  ) {
    super();
  }

  //
  addLocalNotification() {
    if (!this.platform.is("ios")) {
      this.jPush.addLocalNotification(0, "Hello jPush", "jPush", 1, 5000);
    } else {
      this.jPush.addLocalNotificationForIOS(5, "Hello jPush", 1, "localNoti1");
    }
  }

  //
  public initJpush(
    navCtrl: NavController,
    messagePage: any,
    lightProviders: LightProviders
  ) {
    if (this.platform.is("cordova")) {
      // window['plugins'].jPushPlugin.init();
      // if (this.platform.is('ios')) {
      //     window['plugins'].jPushPlugin.setDebugModeFromIos();
      //     window['plugins'].jPushPlugin.setApplicationIconBadgeNumber(0);
      // } else {
      //     window['plugins'].jPushPlugin.setDebugMode(true);
      //     window['plugins'].jPushPlugin.setStatisticsOpen(true);
      // }
      this.jPush.init();

      this.getRegistrationID(lightProviders);

      this.jPushAddEventListener(navCtrl, messagePage);
    }
  }

  private getRegistrationID(lightProviders: LightProviders) {
    this.jPush.getRegistrationID().then(regId => {
      if (regId == "") {
        console.log("");
        setTimeout(() => {
          this.getRegistrationID(lightProviders);
        }, 1000);
      } else {
        console.log(regId);
        this.regId = regId;
        lightProviders.lightLocalCache.set("registrationID", regId);
      }
    });
  }

  private jPushAddEventListener(navCtrl: NavController, messagePage: any) {
    //
    this.jPush.getUserNotificationSettings().then(result => {
      if (result == 0) {
        console.log("");
      } else if (result > 0) {
        console.log("");
      }
    });

    //
    document.addEventListener(
      "jpush.openNotification",
      event => {
        let content = this.platform.is("ios")
          ? event["aps"].alert
          : event["alert"];

        this.toPages(navCtrl, messagePage);

        console.log("jpush.openNotification" + content);
      },
      false
    );

    //
    document.addEventListener(
      "jpush.receiveNotification",
      event => {
        let content = this.platform.is("ios")
          ? event["aps"].alert
          : event["alert"];
        this.clickToastCtrl = true;

        setTimeout(() => {
          const toast = this.toastCtrl.create({
            message: content,
            position: "top",
            showCloseButton: true,
            closeButtonText: "Ok",
            cssClass: 'toast-jpush'
          });

          toast.present();

          toast.onDidDismiss(() => {
            if (this.clickToastCtrl) {
              this.clickToastCtrl = false;
              this.toPages(navCtrl, messagePage);
            }
          });

          setTimeout(() => {
            if (this.clickToastCtrl) {
              this.clickToastCtrl = false;
              toast.dismiss();
            }
          }, 2000);
        }, 500);

        console.log("jpush.receiveNotification" + content);
      },
      false
    );

    //
    document.addEventListener(
      "jpush.receiveMessage",
      event => {
        let message = this.platform.is("ios")
          ? event["content"]
          : event["message"];
        console.log("jpush.receiveMessage" + message);
      },
      false
    );
  }

  //,
  public addAlias(jPushData: any, lightHttp: LightHttp) {
    if (this.platform.is("cordova")) {
      var registrationId = this.regId;
      if (registrationId == "") {
        // console.log("");
        setTimeout(() => {
          this.addAlias(jPushData, lightHttp);
        }, 1000);
      } else {
        lightHttp
          .post(jPushData.url, {
            registrationId: registrationId,
            alias: jPushData.alias
          })
          .then(() => {
            console.log("");
            return registrationId;
          });
      }
    }
  }

  //,
  public delAlias(jPushData: any, lightHttp: LightHttp) {
    if (this.platform.is("cordova")) {
      lightHttp
        .post(jPushData.url, {
          alias: jPushData.alias
        })
        .then(() => {
          console.log("");
        });
    }
  }

  // 
  // jPushOpenNotification(events: Events) {
  //     // 
  //     events.subscribe('jpush.openNotification', content => {
  //         // const childNav = this.nav.getActiveChildNav();
  //         // if (childNav) {
  //         //     const tab = childNav.getSelected();
  //         //     const activeVC = tab.getActive();
  //         //     // if (activeVC.component == AboutPage) {//
  //         //     //   return;
  //         //     // }
  //         //     const activeNav = activeVC.getNav();
  //         //     activeNav.popToRoot({}).then(() => { // 
  //         //         childNav.select(3); // tab
  //         //         const t = childNav.getSelected(); // tab
  //         //         const v = t.getActive(); // tabViewController
  //         //         const n = v.getNav(); // ViewControllerNavController
  //         //         n.push(AboutPage); // 
  //         //     });
  //         // }
  //     });
  // }

  //
  private toPages(navCtrl: NavController, messagePage: any) {
    const childNav = navCtrl.getActiveChildNav();
    if (childNav) {
      const tab = childNav.getSelected();
      const activeVC = tab.getActive();
      // if (activeVC.component == messagePage) {//
      //   return;
      // }
      const activeNav = activeVC.getNav();
      activeNav.popToRoot({}).then(() => {
        // 
        childNav.select(0); // tab
        const t = childNav.getSelected(); // tab
        const v = t.getActive(); // tabViewController
        const n = v.getNav(); // ViewControllerNavController
        n.push(messagePage); // 
      });
    }
  }
}



