    /**
     * 
     * 
     * @param {string} userName  
     * @param {string} userPassword 
     * @param {*} page 
     * @memberof LightLogin
     */
    loginToPage(navCtrl: NavController, userName: string, userPassword: string, page: any): void {
        var that = this;
        if (!!userName && !!userPassword) {
            this.lightProviders.lightUser.userLogin(userName, userPassword).then(() => {
                if (!!page) {
                    that.lightProviders.lightNav.push(navCtrl, page);
                    // navCtrl.push(page)
                    that.lightProviders.lightNav.setRootPage(page);
                }
            }).catch(errText => {
                if (LightStringUtil.contain(errText, "")) {
                    this.lightProviders.lightPop.message(errText);
                } else {
                    this.lightProviders.lightPop.message("");
                }
            });
        } else {
            this.lightProviders.lightPop.message("");
        }
    }

