    updateAppFn(isUpdate: boolean = true, tipShow: boolean = true, listener: (event: ProgressEvent) => any = null) {
        //
        var that = this;
        this.lightAppVersion.getVersionNumber().then(versionData => {
            console.log(" " + versionData);
            if (isUpdate) {
                that.lightProviders.lightHttp.get("Upgrade/UpgradeApp", { version: versionData }).then(value => {
                    var data = LightJsonUtil.toObject(value) as any;
                    if (!!data.VersionNumber) {
                        if (versionData == data.VersionNumber) {
                            if (tipShow) {
                                that.lightProviders.lightPop.message("");
                            }
                        } else {
                            let alert = this.lightProviders.lightPop.alertCtrl.create({
                                title: '',
                                message: '',
                                buttons: [{
                                    text: '',
                                    handler: () => {
                                        //console.log('');
                                        this._downUpdataApp(data.UpgradeURL, versionData, this.lightFileTransfer, this.lightFileOpen, listener);
                                    }
                                }, {
                                    text: '',
                                    role: 'cancel',
                                    handler: () => {
                                        //console.log('');
                                    }
                                }]
                            });
                            alert.present();
                        }
                    } else {
                        if (tipShow) {
                            that.lightProviders.lightPop.message("");
                        }
                    }
                })
            }
        })
    }

