    //
    private _downUpdataApp(downUrl: string, appVersion: any, lightFileTransfer: LightFileTransfer, lightFileOpen: LightFileOpen, listener: (event: ProgressEvent) => any = null) {
        // if (!!this.updateFlag && !!downUrl) {
        let appFileName = "LightOA_" + appVersion + ".apk";
        //app 
        lightFileTransfer.download(downUrl, appFileName, LightFileType.RootFile, listener).then((value) => {
            lightFileOpen.openFile(value).then(() => {
                console.log('apk')
            }).catch(err => {
                this.lightProviders.lightPop.errMessage("apk" + err);
            });
        }).catch(errText => {
            this.lightProviders.lightPop.errMessage("!" + LightJsonUtil.toJson(errText));
        });
        // }
    }

    /**
     * 
     */
    logoutPage(page: any, app: App) {
        this.lightProviders.lightPop.confirm("").then((resultText) => {
            console.log(resultText);
            app.getRootNav().setRoot(page);
            this.lightProviders.lightUser.userExit();
        }).catch((errText) => {
            console.log(errText);
        });
    }

    /**
     * 
     */
    hideBackButton(viewCtrl: ViewController) {
        viewCtrl.showBackButton(false);
    }

