import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LightHttp, LightJsonUtil, LightUser, LightPop, LightLocalCache, LightArrayUtil, LightCamera, LightFileTransfer, LightNav } from '../../../light/light-out/light-out.module';

/**
 * Generated class for the EquipmentEditPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-equipment-edit',
  templateUrl: 'equipment-edit.html',
})
export class EquipmentEditPage {

  ///
  writerData: any;

  ///
  imageArray: Array<any>;

  ///
  code: string;

  ///
  addImageList: Array<any> = [];

  ///
  submitFlag: boolean = false;

  /**
   * 
   * @param {NavController} navCtrl
   * @param {NavParams} navParams
   * @param {LightHttp} lightHttp
   * @memberof ProjectDetailPage
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightUser: LightUser,
    public lightPop: LightPop,
    public localCache: LightLocalCache,
    public lightCamera: LightCamera,
    public lightFileTransfer: LightFileTransfer,
    public lightNav: LightNav
  ) {
    let data = this.navParams.data;
    this.code = data.code;
  }

  ///
  public ngOnInit(): void {
    this._readDataInit();
    this._imageDataInit();
  }

  ///
  private _readDataInit(): void {
    this.lightHttp.get("Equipment/GetCodeForm", { sUserName: this.lightUser.getUserName(), code: this.code, editFlag: true }).then((value) => {
      this.writerData = LightJsonUtil.toObject(value);
    }).catch(() => {
    });
  }

  ///
  private _imageDataInit(): void {
    this.lightHttp.get("Equipment/GetCodePictures", { sUserName: this.lightUser.getUserName(), code: this.code }).then((value) => {
      this.imageArray = LightJsonUtil.toObject(value) as Array<any>;
      if (!!this.imageArray) {
        this.imageArray.forEach(element => {
          element["Name"] = "";
        });
      }
    }).catch(() => {
    });
  }

  /**
   * 
   * @memberof EquipmentWriterComponent
   */
  userSubmit(): void {
    if (!this.submitFlag) {
      this.submitFlag=true;
      if (this.addImageList.length == 0) {
        this._SubmitInfo();

      } else {
        this._submitPicture().then(result => {
          this._SubmitInfo();
        }).catch(errText => {
          this.submitFlag=false;
          this.lightPop.message("" + errText);
        });
      }
    }else{
      this.lightPop.message("..");
    }
  }

  /**
   * 
   *
   * @private
   * @returns {Promise<any>}
   * @memberof EquipmentWriterComponent
   */
  private _submitPicture(): Promise<any> {
    var that = this;
    var uploadUrl = this.lightHttp.getUrl("Equipment/UpLoadPicture");
    var countTemp = 0;
    var result = new Promise(function (resolve, reject) {
      for (var i = 0; i < that.addImageList.length; i++) {
        var element = that.addImageList[i];
        that.lightFileTransfer.upLoad(element["Path"], uploadUrl, null, element).then(tempResult => {
          tempResult["target"]["Path"] = tempResult["message"];
          countTemp++;
          if (countTemp == that.addImageList.length) {
            resolve();
          }
        }).catch(errText => {
          reject("" + errText);
        });
      }
    });
    return result;
  }

  /**
   * 
   * @private
   * @memberof EquipmentWriterComponent
   */
  private _SubmitInfo(): void {
    var jsonString = LightJsonUtil.toJson(this.writerData);
    ///
    this.lightHttp.post("Equipment/SubmitCodePictures", { code: this.code, objectJson: LightJsonUtil.toJson(this._getJsonObject()) }).then(result => {
      ///
      this.lightHttp.post("Equipment/SumbitCodeForm", { sUserName: this.lightUser.getUserName(), code: this.code, content: jsonString })
        .then(result => {
          this.submitFlag=false;
          this.lightPop.message("");
          this.localCache.set("writerFlag", "true");
          this.navCtrl.pop();
        }).catch(errText => {
          this.lightPop.message("" + errText);
        });
    }).catch(errText => {
      this.lightPop.message("" + errText);
    });
  }

  /**
   * Json
   * @private
   * @returns {Array<any>}
   * @memberof EquipmentWriterComponent
   */
  private _getJsonObject(): Array<any> {
    var result: Array<any> = [];
    this.imageArray.forEach(element => {
      result.push(element);
    });
    this.addImageList.forEach(element => {
      result.push(element);
    });
    return result;
  }

  /**
   * 
   * @param {*} pictureObject
   * @memberof EquipmentWriterComponent
   */
  removePictureClick(pictureObject: any): void {
    if (!!pictureObject) {
      this.lightPop.confirm("").then(result => {
        LightArrayUtil.remove(this.imageArray, pictureObject);
      }).catch(errText => {

      });
    }
  }

  /**
   * 
   * @param {*} pictureObject
   * @memberof EquipmentWriterComponent
   */
  removeAddPictureClick(pictureObject: any): void {
    if (!!pictureObject) {
      this.lightPop.confirm("").then(result => {
        LightArrayUtil.remove(this.addImageList, pictureObject);
      }).catch(errText => {

      });
    }
  }

  /**
   * 
   * @memberof EquipmentWriterComponent
   */
  addPictureClick(): void {
    //
    this.lightCamera.getPicture().then(result => {
      this.addImageList.push({ Path: result });
    }).catch(errText => {
      this.lightPop.message("" + errText);
      //alert(""+LightJsonUtil.toJson(errText));
    });
  }

}



