import { LightHttp } from './../light/light-providers/providers/lightHttp';
import { Component, ViewChild } from "@angular/core";
import {
  Platform,
  NavController,
  IonicApp,
  App,
  ViewController,
  ToastController
} from "ionic-angular";

import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";

import { TabsPage, LoginPage } from "../pages/common/index";

import {
  LightConfig,
  LightUser,
  LightJPush,
  LightProviders,
  LightPermissisons,
  LightKeyboard,
  LightExit,
  LightApp,
  LightJsonUtil
} from "../light/light-out/light-out.module";

//import { Deeplinks } from '@ionic-native/deeplinks/ngx';

declare let WeixinJSBridge;
declare let wx;

@Component({
  templateUrl: "app.html"
})
export class MyApp {
  @ViewChild("myNav") nav: NavController;

  rootPage: any = null;

  constructor(
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    public platform: Platform,
    public lightConfig: LightConfig,
    public lightJPush: LightJPush,
    public lightProviders: LightProviders,
    public lightApp: LightApp,
    public lightPermissisons: LightPermissisons,
    public lightKeyboard: LightKeyboard,
    public lightUser: LightUser,
    private ionicApp: IonicApp,
    private lightExit: LightExit,
    private _app: App,
    private toastCtrl: ToastController,
    private lightHttp: LightHttp
  ) {
    platform.ready().then(() => {
      statusBar.styleDefault();

      splashScreen.hide();

      this._init();

      this._cacheInit();
    });
  }

  /**
   * 
   * @private
   * @memberof MyApp
   */
  private _init(): void {
    //this.lightConfig.baseUrl = "http://172.16.50.135:27116/"; //
    this.lightConfig.baseUrl = "https://183.3.132.227:17116/"; //  
    // this.lightConfig.baseUrl = "http://172.16.50.135:17112/"; //
    // this.lightConfig.baseUrl = "http://172.16.50.135:17113/"; //
    // this.lightConfig.baseUrl = "http://218.25.36.152:10003/";  //
    // this.lightConfig.baseUrl = "http://61.136.243.46:8092/";  //

    this.lightConfig.timeOut = 2000;

    this.lightConfig.httpTimeOut = 60000;

    //
    this.lightExit.registerBackButtonAction(this.ionicApp, this.nav);

    //
    this.lightKeyboard.initKeyboard(window);

    // 
    this.wxBack();

    // 
    this.wxKeyboard();

    // 
    this.initLogin();

    //
    this.lightJPush.initJpush(
      this.nav,
      "LightMessageComponent",
      this.lightProviders
    );
  }

  /**
   * 
   * @private
   * @memberof MyApp
   */
  private _cacheInit(): void {}

  // /**
  //  * 
  //  * @private
  //  * @memberof MyApp
  //  */
  // private permissonCheck(): void {
  //   // this.lightPermissisons
  // }

  /**
   *
   *
   * @private
   * @memberof MyApp
   */
  private wxBack() {
    if (!this.platform.is("cordova")) {
      window.onpopstate = () => {
        console.log("");

        // 
        // const activePortal =
        //   this.ionicApp._overlayPortal.getActive() ||
        //   this.ionicApp._loadingPortal.getActive();
        // if (activePortal) {
        //   activePortal.dismiss();
        //   activePortal.onDidDismiss(() => { });

        const childNav = this.nav.getActiveChildNavs()[0]; // tabs,this.nav,tabs
        if (!childNav) {
          WeixinJSBridge.call("closeWindow");
          console.log("");
          return;
        }
        const tab = childNav.getSelected(); // tab
        const activeVC = tab.getActive(); // tabViewController
        const activeNav = activeVC.getNav(); // ViewControllerNavController

        return activeNav.canGoBack()
          ? activeNav.pop()
          : WeixinJSBridge.call("closeWindow");
      };

      // 
      history.pushState(null, null, null);
      this._app.viewDidEnter.subscribe((view: ViewController) => {
        if (view.index > 0) {
          history.pushState(null, null, null);
        }
      });

      // ios
      // if (typeof WeixinJSBridge == "undefined"){
      //     document.addEventListener('WeixinJSBridgeReady', () => {
      //         WeixinJSBridge.call('hideToolbar');
      //         console.log('WeixinJSBridgeReady hideToolbar');
      //     }, false);
      // }else{
      //     WeixinJSBridge.call('hideToolbar');
      // };
    }
  }

  /**
   *-
   *
   * @private
   * @memberof MyApp
   */
  private initLogin() {
    // 
    let ua = navigator.userAgent.toLowerCase();
    let isWeixin = ua.indexOf("micromessenger") != -1;
    // TODO:sinfo
    if (isWeixin = false) {
      // 
      if (location.href.includes("&state=login")) {
        // codeoauth2code
        let codeReg = /(?:code\=)(\S+?)(?:&state)/;
        this.lightHttp
          .post('code', {
            code: codeReg.exec(location.href)[1],
          })
          .then(function(data) {
            // 
            let userInfo = LightJsonUtil.toObject(data);
            console.log(userInfo);
            this.rootPage = TabsPage;
          })
          .catch(function(errText: string) {
            // 
            console.log(errText);
          });
      } else {
        console.log(localStorage.getItem('wxUser'))
        // UserIdoauth2code
        this.lightHttp
          .post('', {
            UserId: localStorage.getItem('wxUser') ? localStorage.getItem('wxUser') : '',
          })
          .then(function(data) {
            // 
            let userInfo = LightJsonUtil.toObject(data);
            console.log(userInfo);
            this.rootPage = TabsPage;
          })
          .catch(function(errText: string) {
            // 
            console.log(errText);
          });
      }
    } else {
      // 
      this.lightUser
        .autoLoginCheck()
        .then(() => {
          this.rootPage = TabsPage;
          if (window["cordova"]) {
            this.lightApp.updateAppFn(false, false);
          }
        })
        .catch(() => {
          this.rootPage = LoginPage;
        });
    }
  }

  /**
   *
   *
   * @private
   * @memberof MyApp
   */
  private wxKeyboard() {
    let ua = navigator.userAgent.toLowerCase();
    let isWeixin = ua.indexOf("micromessenger") != -1;
    let isAndroid = ua.indexOf("android") != -1;
    let isIos = ua.indexOf("iphone") != -1 || ua.indexOf("ipad") != -1;
    if (isWeixin) {
      let wxKeyboardShow = new Event("wxKeyboardShow");
      let wxKeyboardHide = new Event("wxKeyboardHide");
      if (isAndroid) {
        //
        var originalHeight =
          document.documentElement.clientHeight || document.body.clientHeight;
        window.onresize = () => {
          //
          var resizeHeight =
            document.documentElement.clientHeight || document.body.clientHeight;
          if (resizeHeight - 0 < originalHeight - 0) {
            //
            window.dispatchEvent(wxKeyboardShow);
          } else {
            //
            window.dispatchEvent(wxKeyboardHide);
          }
        };
      }
      if (isIos) {
        document.body.addEventListener("focusin", () => {
          //
          window.dispatchEvent(wxKeyboardShow);
        });
        document.body.addEventListener("focusout", () => {
          //
          window.dispatchEvent(wxKeyboardHide);
        });
      }
    }
  }
}



