import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { LightPop } from "../../../light/light-providers/providers";
import { Http } from "@angular/http";

/**
 * Generated class for the LogDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-log-detail",
  templateUrl: "log-detail.html"
})
export class LogDetailPage {
  // 
  showStatus = true;
  calendar = {
    currentDate: 3,
    daysInLastMonth: [29, 30, 31],
    daysInThisMonth: [1, 2, 3, 4, 5, 6, 7, 8],
    daysInNextMonth: [29, 30, 31]
  };

  // 
  selectDay = "2019-03-21";

  // 
  logTemplate = [
    {
      itemName: "",
      itemNumber: "11111",
      itemTime: "",
      itemContent: "",
      extendFields: []
    },
    {
      itemName: "",
      itemNumber: "22222",
      itemTime: "",
      itemContent: "",
      extendFields: []
    },
    {
      itemName: "",
      itemNumber: "33333",
      itemTime: "",
      itemContent: "",
      extendFields: []
    }
  ];

  // 
  itemList = [];

  // 
  titleName = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightPop: LightPop,
    public http: Http
  ) {}

  ionViewDidLoad() {
    console.log("ionViewDidLoad LogDetailPage");
  }

  public ngOnInit(): void {
    if (this.navParams.get("CODE")) {
      // 
      this.titleName = "";
      this.http
        .get("http://" + location.host + "/assets/json/log/itemList.json")
        .subscribe(res => {
          this.itemList = res.json();
        });
    } else {
      // 
      this.titleName = "";
      this.addItem(true);
    }
  }

  /**
   *
   *
   * @param {boolean} [withItemName=false] 
   * @memberof LogDetailPage
   */
  public addItem(withItemName = false) {
    // 
    let item = [
      {
        fieldName: "itemName",
        fieldLabel: "",
        fieldType: "select",
        fieldValue: ""
      },
      {
        fieldName: "itemNumber",
        fieldLabel: "",
        fieldType: "select",
        fieldValue: ""
      },
      {
        fieldName: "itemTime",
        fieldLabel: "",
        fieldType: "text",
        fieldValue: ""
      },
      {
        fieldName: "itemContent",
        fieldLabel: "",
        fieldType: "text",
        fieldValue: ""
      }
    ];
    this.itemList.push(item);
    if (withItemName) {
      setTimeout(() => {
        this.itemList[
          this.itemList.length - 1
        ][0].fieldValue = this.logTemplate[0].itemName;
      }, 100);
    }
  }

  /**
   *
   *
   * @param {*} index 
   * @param {*} slidingItem 
   * @memberof LogDetailPage
   */
  public deleteItem(index, slidingItem) {
    if (this.itemList.length == 1) {
      this.lightPop
        .message("")
        .then(result => {
          slidingItem.close();
        })
        .catch(errText => {});
    } else {
      this.lightPop
        .confirm("")
        .then(result => {
          this.itemList.splice(index, 1);
        })
        .catch(errText => {
          slidingItem.close();
        });
    }
  }

  /**
   *
   *
   * @param {string} fieldName 
   * @param {number} index 
   * @param {*} logFieldValues 
   * @memberof LogDetailPage
   */
  public logRule(fieldName: string, index: number, ...logFieldValues) {
    switch (fieldName) {
      // 
      case "itemName":
        ((index, logFieldValues) => {
          let [itemName, itemTime] = logFieldValues;
          this.itemList[index].map(log => {
            switch (log.fieldName) {
              case "itemNumber":
                log.fieldValue = this.logTemplate.find(log => {
                  return log.itemName == itemName;
                }).itemNumber;
                break;
              default:
                break;
            }
          });
        })(index, logFieldValues);
        break;
      // 
      case "itemNumber":
        ((index, logFieldValues) => {
          let [itemNumber] = logFieldValues;
          this.itemList[index].map(log => {
            switch (log.fieldName) {
              case "itemName":
                log.fieldValue = this.logTemplate.find(log => {
                  return log.itemNumber == itemNumber;
                }).itemName;
                break;
              default:
                break;
            }
          });
        })(index, logFieldValues);
        break;
      default:
        break;
    }
  }

  /**
   *
   *
   * @param {*} log 
   * @param {*} fieldName 
   * @returns
   * @memberof LogDetailPage
   */
  public getFieldValue(log: any, fieldName) {
    return log.find(field => {
      return field.fieldName == fieldName;
    }).fieldValue;
  }

  /**
   *
   *
   * @memberof LogDetailPage
   */
  public submitLog() {
    this.lightPop
      .message(this.navParams.get("CODE") ? "" : "", "", false)
      .then(result => {
        this.navCtrl.pop();
      })
      .catch(errText => {});
  }

  // public changeStatus() {
  //   this.showStatus = !this.showStatus;
  // }

  // 
  // public getEventsOn(day) {
  //   return [
  //     {
  //       color: "red"
  //     }
  //   ];
  // }
}



