import { ToastController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { Platform, IonicApp, Events, Keyboard, NavController } from 'ionic-angular';
import { LightPop } from './lightPop';
import { LightMinimize } from '../../light-nation/light-nation.module';

//
@Injectable()
export class LightExit {

  public backButtonPressed = false;

  private timeOutOp: any;

  /**
   * 
   * @param {Platform} platform
   * @param {LightPop} lightPop
   * @memberof LightExit
   */
  constructor(
    private platform: Platform,
    private events: Events,
    private keyboard: Keyboard,
    public lightMinimize: LightMinimize,
    private lightPop: LightPop,
    private toastCtrl: ToastController
  ) {

  }

  /**
   * app
   *
   * @memberof LightExit
   */
  public appExit(): void {
    this.lightPop.confirm("", "").then(() => {
      this.platform.exitApp();
    }).catch(() => {

    });
  }

  //
  private minimizeOrExit(minimize: boolean) {
    if (this.backButtonPressed) {
      //true4APP
      if (minimize) {
        this.lightMinimize.minimize();
      } else {
        this.platform.exitApp();
      }
    } else {
      if (!!this.timeOutOp) {
        clearTimeout(this.timeOutOp);
      }
      this.backButtonPressed = true;
      let toast = this.toastCtrl.create({
        message: "",
        duration: 1000,
        position: "bottom",
        cssClass: "toast-exit"
      });
      toast.present();
      this.timeOutOp = setTimeout(() => {
        this.backButtonPressed = false;
      }, 2001); //2false
    }
  }

  /**
   * 
   */
  registerBackButtonAction(ionicApp: IonicApp, navCtrl: NavController, minimize = false) {
    if (this.platform.is('ios')) {
      return;
    }
    this.platform.registerBackButtonAction(() => {
      this.events.publish('android:backButtonAction');
      if (this.keyboard.isOpen()) { // 
        this.keyboard.close();
        return;
      }
      // toastloadingOverlay
      // this.ionicApp._toastPortal.getActive() ||this.ionicApp._loadingPortal.getActive()|| this.ionicApp._overlayPortal.getActive()
      const activePortal =
        ionicApp._modalPortal.getActive() ||
        ionicApp._overlayPortal.getActive() ||
        ionicApp._loadingPortal.getActive();
      if (activePortal) {
        activePortal.dismiss();
        activePortal.onDidDismiss(() => { });
        return;
      }
      const childNav = navCtrl.getActiveChildNavs()[0]; // tabs,this.nav,tabs
      if (!childNav) {
        this.minimizeOrExit(minimize)
        return;
      }
      const tab = childNav.getSelected(); // tab
      const activeVC = tab.getActive(); // tabViewController
      const activeNav = activeVC.getNav(); // ViewControllerNavController
      return activeNav.canGoBack() ? activeNav.pop() : this.minimizeOrExit(minimize);
    }, 1);
  }

}



